import React, {Fragment} from 'react';
import PokemonCard from './PokemonCard';

export default function PokemonList({isShiny, allPokemon}) {
  return (
    <Fragment>
      {allPokemon ? (
        <Fragment>
          {allPokemon.map(pokemon => (
            <PokemonCard
              {...pokemon}
              key={pokemon.name}
              isShiny={isShiny}
              onClick={() => {
                this.setState({currentPokemon: pokemon});
              }}
            />
          ))}
        </Fragment>
      ) : (
        <h2>Loading Pokedex</h2>
      )}
    </Fragment>
  );
}
